import { defineStore } from 'pinia'

export const useAppStore = defineStore('app', {
  state: () => ({
    tgInitData: null,
    apiBaseUrl: 'https://tough-rivers-vanish.loca.lt', // <-- —Å—é–¥–∞ —Å–≤–æ–π URL
  }),
  actions: {
    initTelegram() {
      // –µ—Å–ª–∏ –µ—â—ë –Ω–µ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä–æ–≤–∞–Ω–æ
      if (!this.tgInitData) {
        const tg = window.Telegram.WebApp
        tg.ready()
        tg.expand()

        // –∑–∞–±–∏—Ä–∞–µ–º initData
        this.tgInitData = tg.initData || null
        console.log(typeof(this.tgInitData))
      }
    }
  }
})
